import React from 'react'

interface SpinnerProps {
    color?: 'red' | 'green' | 'blue' | 'black' | 'yellow'
    className?: string
}

const choiseColor = (color: SpinnerProps['color']) => {
    switch (color) {
        case 'red':
            return 'color-red-200'
        case 'green':
            return 'color-green-200'
        case 'blue':
            return 'color-blue-200'
        case 'black':
            return 'color-black-200'
        case 'yellow':
            return 'color-yellow-200'
        default:
            return ''
    }
}

export const Spinner = ({ color, className, ...props }: SpinnerProps) => {
    return <div className={`spinner border  `}></div>
}
